import React, { Component } from "react";

class Tick extends Component {
  constructor(props) {
    super(props);
    this.state = {
      time: new Date().toLocaleTimeString()
    };
  }
  Clock = setInterval(() => {
    this.setState({
      time: new Date().toLocaleTimeString()
    });
  }, 1000);
  // 
  componentWillUnmount() {
    // 
    clearInterval(this.Clock);
  }
  render() {
    const { time } = this.state;
    return (
      <div>
        <h1>Hello, world!</h1>
        <h2>It is {time}.</h2>
      </div>
    );
  }
}

export default Tick;
